<script setup>
import { defineProps, defineEmits, ref, onMounted } from "vue";
import { useRouter } from "vue-router";
import { ColorPicker } from "vue3-colorpicker";
import "vue3-colorpicker/style.css";
import { useStorage } from "vue3-storage";
// import { useCookies } from "vue3-cookies";

const storage = useStorage();
// const cookies = useCookies();
// const randomDataList = ref([]);
// console.log(cookies);
const router = useRouter();
const props = defineProps({
  settingsData: Object,
});
const emits = defineEmits(["emitClose"]);
const list = ref([]);
const winnersNum = ref(1);
const selectBg = ref(0);
const bgColor = ref("rgb(66, 165, 246)");
const bgSrc = ref("");
const selectFont = ref(0);
const fontColor = ref("rgb(255, 255, 255)");
const title = ref("");
const detail = ref("");
let randomList = ref([]);
const img = new Image();
const isVertical = ref(false);
const isLoading = ref(false);

const bgImage = (event) => {
  var input = event.target;
  if (input.files && input.files[0]) {
    var reader = new FileReader();
    reader.onload = (e) => {
      bgSrc.value = e.target.result;
      img.src = e.target.result;
    };
    reader.readAsDataURL(input.files[0]);
  }
};
const randomPick = (min, max) => {
  const num = Math.floor(Math.random() * (max - min + 1)) + min;
  if (!num) {
    window.alert("!");
    return;
  }
  randomList.value.push(list.value[num]);
  list.value.splice(num, 1);
};
const saveData = () => {
  imageSizeChange(img);
  imageRate();
  if (winnersNum.value > props.settingsData.list.length) {
    window.alert("      .");
    return;
  }
  if (!title.value) {
    window.alert(" .");
    return;
  }
  for (let i = 0; i < winnersNum.value; i++) {
    randomPick(1, list.value.length - 1);
  }
  if (selectBg.value === 0) {
    bgSrc.value = "";
  }
  const data = {
    list: props.settingsData.list,
    num: winnersNum.value,
    selectBg: selectBg.value,
    bgColor: bgColor.value,
    selectFont: selectFont.value,
    fontColor: fontColor.value,
    title: title.value,
    detail: detail.value,
    winner: randomList.value,
    bgSrc: bgSrc.value,
    isVertical: isVertical.value,
  };
  isLoading.value = true;
  setTimeout(() => {
    // randomDataList.value.push(data);
    storage.setStorageSync("randomData", data);
    // storage.setStorageSync("randomDataList", randomDataList.value);
    emits("emitData", false);
    emits("emitClose", false);
    isLoading.value = false;
    router.push("/lucky-draw/draw");
  }, 1000);
};

const imageRate = () => {
  const browserRate = window.innerWidth / window.innerHeight;
  const imgRate = img.width / img.height;
  if (browserRate > imgRate) {
    isVertical.value = false;
  } else {
    isVertical.value = true;
  }
};
const imageSizeChange = (image) => {
  const canvas = document.createElement("canvas");
  const max_size = 900;
  let width = image.width;
  let height = image.height;
  if (width > height) {
    //   
    if (width > max_size) {
      height *= max_size / width;
      width = max_size;
    }
  } else {
    //   
    if (height > max_size) {
      width *= max_size / height;
      height = max_size;
    }
  }
  canvas.width = width;
  canvas.height = height;
  canvas.getContext("2d").drawImage(image, 0, 0, width, height);
  bgSrc.value = canvas.toDataURL("image/jpeg", 0.5);
};

onMounted(() => {
  list.value = JSON.parse(JSON.stringify(props.settingsData.list));
  winnersNum.value = props.settingsData.num || 1;
  selectBg.value = props.settingsData.selectBg || 0;
  bgColor.value = props.settingsData.bgColor || "rgb(66, 165, 246)";
  bgSrc.value = props.settingsData.bgSrc || "";
  img.src = props.settingsData.bgSrc;
  isVertical.value = props.settingsData.isVertical || false;
  selectFont.value = props.settingsData.selectFont || 0;
  fontColor.value = props.settingsData.fontColor || "rgb(255, 255, 255)";
  title.value = props.settingsData.title || "";
  detail.value = props.settingsData.detail || "";
  // if (storage.getStorageSync("randomDataList")) {
  //   randomDataList.value = storage.getStorageSync("randomDataList");
  // }
});
</script>
<template>
  <article id="settings" class="pop-up">
    <div class="content">
      <h3></h3>
      <table>
        <tbody>
          <tr>
            <th> </th>
            <td>
              <input
                type="number"
                min="1"
                :max="list.length"
                v-model="winnersNum"
              />
            </td>
          </tr>
          <tr>
            <th> </th>
            <td>
              <input type="radio" v-model="selectBg" :value="0" id="color" />
              <label for="color"></label>
              <input type="radio" v-model="selectBg" :value="1" id="image" />
              <label for="image"></label>
            </td>
          </tr>
          <tr v-if="selectBg === 0">
            <th> </th>
            <td>
              <color-picker v-model:pureColor="bgColor" />
            </td>
          </tr>
          <tr v-if="selectBg === 1">
            <th> </th>
            <td>
              <input type="file" @change="bgImage" />
              <img
                class="preview-image"
                v-if="bgSrc"
                :src="bgSrc"
                :alt="title"
              />
            </td>
          </tr>
          <tr>
            <th>  </th>
            <td>
              <input
                type="radio"
                v-model="selectFont"
                :value="0"
                id="sansSerif"
              />
              <label for="sansSerif" class="font-sans-serif"></label>
              <input type="radio" v-model="selectFont" :value="1" id="serif" />
              <label for="serif" class="font-serif"></label>
              <input
                type="radio"
                v-model="selectFont"
                :value="2"
                id="calligraphic"
              />
              <label for="calligraphic" class="font-calligraphic"></label>
            </td>
          </tr>
          <tr>
            <th>  </th>
            <td>
              <color-picker v-model:pureColor="fontColor" />
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <input
                type="text"
                placeholder=" "
                v-model="title"
                :class="
                  selectFont === 0
                    ? 'font-sans-serif'
                    : selectFont === 1
                    ? 'font-serif'
                    : 'font-calligraphic'
                "
              />
            </td>
          </tr>
          <tr>
            <th></th>
            <td>
              <input
                type="text"
                v-model="detail"
                placeholder=" "
              />
            </td>
          </tr>
        </tbody>
      </table>
      <p class="btn-box">
        <button @click="emits('emitClose', false)"></button>
        <button @click="saveData()"></button>
      </p>
    </div>
    <div v-if="isLoading" class="loading">
      <!-- <div class="loading"> -->
      <p>
        <span></span>
        <span></span>
        <span></span>
        <span></span>
      </p>
    </div>
  </article>
</template>
