<script setup>
// import { defineProps, defineEmits, ref, onMounted } from "vue";
import { ref, onMounted, defineEmits } from "vue";
import { useStorage } from "vue3-storage";

const emits = defineEmits(["emitClose", "emitData"]);

const storage = useStorage();
const randomDataList = ref([]);

const emitData = (val) => {
  emits("emitData", val);
  emits("emitClose", false);
};

onMounted(() => {
  if (storage.getStorageSync("randomDataList")) {
    randomDataList.value = storage.getStorageSync("randomDataList");
  }
});
</script>
<template>
  <article id="dataList" class="pop-up">
    <div class="content">
      <h3>  </h3>
      <table>
        <thead>
          <tr>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(random, rIdx) in randomDataList" :key="rIdx">
            <td>{{ rIdx + 1 }}</td>
            <td>{{ random.title }}</td>
            <td>{{ random.list.length }}</td>
            <td><button @click="emitData(random)"></button></td>
          </tr>
        </tbody>
      </table>
      <p class="btn-box">
        <button @click="emits('emitClose', false)"></button>
      </p>
    </div>
  </article>
</template>
