<script setup>
import { ref, onMounted } from "vue";
import SettingComponent from "../components/Setting.vue";
import DataListComponent from "../components/DataList.vue";
import { useStorage } from "vue3-storage";
const storage = useStorage();

const winner = ref("");
const openSettings = ref(false);
const openDetail = ref(false);
const settingsData = ref({});
const setWinners = () => {
  if (!winner.value) {
    window.alert(" .");
    return;
  }
  if (!settingsData.value.list) {
    settingsData.value.list = [];
  }
  settingsData.value.list.push(winner.value);
  winner.value = "";
};
const settings = () => {
  if (settingsData.value.list.length < 1) {
    window.alert(" .");
    return;
  }
  openSettings.value = true;
};
onMounted(() => {
  if (storage.getStorageSync("randomData")) {
    const data = storage.getStorageSync("randomData");
    settingsData.value = data;
  }
});
</script>
<template>
  <article id="introInput">
    <div class="input-box">
      <ul>
        <li class="input">
          <input
            type="text"
            v-model="winner"
            @keyup.enter="setWinners()"
            placeholder=" ."
          />
        </li>
        <li class="input-button">
          <button @click="setWinners()"></button>
        </li>
        <li class="button-out reset" @click="settingsData.list = []">
          <button> </button>
        </li>
        <!-- <li class="button-out prev" @click="openDetail = true">
          <button> </button>
        </li> -->
        <li @click="settings()" class="random-button">
          <button> </button>
        </li>
      </ul>
    </div>
    <div class="winners-list">
      <ul>
        <li v-for="(winner, wIdx) in settingsData.list" :key="wIdx">
          <span class="winner-number">{{ wIdx + 1 }}.</span>
          <span class="winner">{{ winner }}</span>
          <span
            @click="settingsData.list.splice(wIdx, 1)"
            class="material-icons cancel"
          >
            close
          </span>
        </li>
      </ul>
    </div>
  </article>
  <SettingComponent
    v-if="openSettings"
    :settingsData="settingsData"
    @emitClose="(val) => (openSettings = val)"
  />
  <DataListComponent
    v-if="openDetail"
    @emitData="(val) => (settingsData = val)"
    @emitClose="(val) => (openDetail = val)"
  />
</template>
